					</view>
				</view>
				<view class="change-script" @tap="changeScript">
					<text>Êõ¥Êç¢</text>
				</view>
			</view>
		</view>

		<!-- ÈÄâÊã©Êó•Êúü -->
		<view class="date-section" v-if="selectedScript">
			<view class="section-title">
				<text class="title-text">üìÖ ÈÄâÊã©Êó•Êúü</text>
			</view>
			<scroll-view scroll-x="true" class="date-scroll">
				<view class="date-list">
					<view class="date-item" 
						v-for="date in availableDates" 
						:key="date.dateString"
						:class="{'active': selectedDate === date.dateString}"
						@tap="selectDate(date.dateString)">
						<text class="date-weekday">{{date.weekday}}</text>
						<text class="date-day">{{date.day}}</text>
						<text class="date-month">{{date.month}}</text>
					</view>
				</view>
			</scroll-view>
		</view>

		<!-- ÈÄâÊã©Âú∫Ê¨° -->
		<view class="sessions-section" v-if="selectedDate">
			<view class="section-title">
				<text class="title-text">‚è∞ ÈÄâÊã©Âú∫Ê¨°</text>
			</view>
			<view class="sessions-list">
				<view class="session-item" 
					v-for="session in availableSessions" 
					:key="session.id"
					:class="{'active': selectedSession && selectedSession.id === session.id, 'disabled': session.currentPlayers >= session.maxPlayers}"
					@tap="selectSession(session)">
					<view class="session-time">
						<text class="time-text">{{session.startTime}}</text>
						<text class="time-period">{{session.period}}</text>
					</view>
					<view class="session-info">
						<text class="session-players">{{session.currentPlayers}}/{{session.maxPlayers}}‰∫∫</text>
						<text class="session-status" :class="{'full': session.currentPlayers >= session.maxPlayers}">
							{{session.currentPlayers >= session.maxPlayers ? 'Â∑≤Êª°' : 'ÂèØÁ∫¶'}}
						</text>
					</view>
					<view class="session-price">
						<text>¬•{{session.price}}</text>
					</view>
				</view>
			</view>
		</view>

		<!-- ËÅîÁ≥ªÊñπÂºè -->
		<view class="contact-section" v-if="selectedSession">
			<view class="section-title">
				<text class="title-text">üìû ËÅîÁ≥ªÊñπÂºè</text>
			</view>
			<view class="form-item">
				<text class="form-label">ÂßìÂêç</text>
				<input class="form-input" v-model="contactInfo.name" placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÂßìÂêç" />
			</view>
			<view class="form-item">
				<text class="form-label">ÊâãÊú∫Âè∑</text>
				<input class="form-input" v-model="contactInfo.phone" placeholder="ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑Á†Å" type="number" />
			</view>
			<view class="form-item">
				<text class="form-label">Â§áÊ≥®</text>
				<textarea class="form-textarea" v-model="contactInfo.note" placeholder="Êúâ‰ªÄ‰πàÁâπÊÆäË¶ÅÊ±ÇÂèØ‰ª•Âú®ËøôÈáåËØ¥ÊòéÔºàÈÄâÂ°´Ôºâ"></textarea>
			</view>
		</view>

		<!-- È¢ÑÁ∫¶È°ªÁü• -->
		<view class="notice-section" v-if="selectedSession">
			<view class="section-title">
				<text class="title-text">üìã È¢ÑÁ∫¶È°ªÁü•</text>
			</view>
			<view class="notice-content">
				<view class="notice-item">
					<text class="notice-text">‚Ä¢ ËØ∑ÊèêÂâç15ÂàÜÈíüÂà∞Â∫óÔºåÂáÜÂ§áË∫´‰ªΩËØÅ‰ª∂</text>
				</view>
				<view class="notice-item">
					<text class="notice-text">‚Ä¢ Ê∏∏ÊàèËøáÁ®ã‰∏≠ËØ∑ÂÖ≥Èó≠ÊâãÊú∫ÊàñË∞ÉËá≥ÈùôÈü≥</text>
				</view>
				<view class="notice-item">
					<text class="notice-text">‚Ä¢ Â¶ÇÈúÄÂèñÊ∂àÈ¢ÑÁ∫¶ÔºåËØ∑ÊèêÂâç2Â∞èÊó∂ËÅîÁ≥ªÂÆ¢Êúç</text>
				</view>
				<view class="notice-item">
					<text class="notice-text">‚Ä¢ ËøüÂà∞Ë∂ÖËøá30ÂàÜÈíüÂ∞ÜËßÜ‰∏∫Ëá™Âä®ÂèñÊ∂à</text>
				</view>
			</view>
		</view>

		<!-- ‰ª∑Ê†ºÊòéÁªÜ -->
		<view class="price-section" v-if="selectedSession">
			<view class="section-title">
				<text class="title-text">üí∞ ‰ª∑Ê†ºÊòéÁªÜ</text>
			</view>
			<view class="price-detail">
				<view class="price-row">
					<text class="price-label">ÂâßÊú¨Ë¥πÁî®</text>
					<text class="price-value">¬•{{selectedSession.price}}/‰∫∫</text>
				</view>
				<view class="price-row">
					<text class="price-label">È¢ÑÁ∫¶‰∫∫Êï∞</text>
					<view class="player-count">
						<view class="count-btn" @tap="decreaseCount">-</view>
						<text class="count-text">{{playerCount}}</text>
						<view class="count-btn" @tap="increaseCount">+</view>
					</view>
				</view>
				<view class="price-row total">
					<text class="price-label">ÊÄªËÆ°</text>
					<text class="price-value total-price">¬•{{totalPrice}}</text>
				</view>
			</view>
		</view>

		<!-- Â∫ïÈÉ®Á°ÆËÆ§ÊåâÈíÆ -->
		<view class="bottom-bar" v-if="selectedSession">
			<view class="total-info">
				<text class="total-text">ÊÄªËÆ°Ôºö¬•{{totalPrice}}</text>
			</view>
			<view class="confirm-btn" @tap="confirmReservation">
				<text>Á°ÆËÆ§È¢ÑÁ∫¶</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				scriptId: null,
				sessionId: null,
				selectedScript: null,
				selectedDate: '',
				selectedSession: null,
				playerCount: 1,
				contactInfo: {
					name: '',
					phone: '',
					note: ''
				},
				scripts: [
					{
						id: 1,
						name: 'Âπ¥ËΩÆ',
						cover: '/static/script1.jpg',
						tags: ['ÊÇ¨Áñë', 'Êé®ÁêÜ'],
						playerCount: 6,
						duration: 4,
						price: 88
					},
					{
						id: 2,
						name: '‰∫ë‰Ωø',
						cover: '/static/script2.jpg',
						tags: ['Âè§È£é', 'ÊÉÖÊÑü'],
						playerCount: 7,
						duration: 5,
						price: 98
					},
					{
						id: 3,
						name: 'Á¨¨‰∏É‰∏™Â´åÁñë‰∫∫',
						cover: '/static/script3.jpg',
						tags: ['Áé∞‰ª£', 'ÊÇ¨Áñë'],
						playerCount: 7,
						duration: 4,
						price: 78
					}
				],
				availableDates: [],
				availableSessions: []
			}
		},
		computed: {
			totalPrice() {
				if (!this.selectedSession) return 0;
				return this.selectedSession.price * this.playerCount;
			}
		},
		onLoad(options) {
			if (options.scriptId) {
				this.scriptId = options.scriptId;
				this.selectScriptById(options.scriptId);
			}
			if (options.sessionId) {
				this.sessionId = options.sessionId;
			}
			this.generateAvailableDates();
		},
		methods: {
			selectScriptById(scriptId) {
				const script = this.scripts.find(s => s.id == scriptId);
				if (script) {
					this.selectedScript = script;
				}
			},
			selectScript(script) {
				this.selectedScript = script;
				this.selectedDate = '';
				this.selectedSession = null;
			},
			changeScript() {
				this.selectedScript = null;
				this.selectedDate = '';
				this.selectedSession = null;
			},
			generateAvailableDates() {
				const dates = [];
				const today = new Date();
				const weekdays = ['Êó•', '‰∏Ä', '‰∫å', '‰∏â', 'Âõõ', '‰∫î', 'ÂÖ≠'];
				const months = ['01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', '12'];
				
				for (let i = 0; i < 7; i++) {
					const date = new Date(today);
					date.setDate(today.getDate() + i);
					
					dates.push({
						dateString: date.toISOString().split('T')[0],
						weekday: weekdays[date.getDay()],
						day: date.getDate(),
						month: months[date.getMonth()]
					});
				}
				
				this.availableDates = dates;
			},
			selectDate(dateString) {
				this.selectedDate = dateString;
				this.selectedSession = null;
				this.loadAvailableSessions();
			},
			loadAvailableSessions() {
				// Ê®°ÊãüËé∑ÂèñÂèØÁî®Âú∫Ê¨°Êï∞ÊçÆ
				this.availableSessions = [
					{
						id: 1,
						startTime: '14:00',
						period: '‰∏ãÂçàÂú∫',
						currentPlayers: 3,
						maxPlayers: this.selectedScript ? this.selectedScript.playerCount : 6,
						price: this.selectedScript ? this.selectedScript.price : 88
					},
					{
						id: 2,
						startTime: '19:00',
						period: 'ÊôöÂú∫',
						currentPlayers: 5,
						maxPlayers: this.selectedScript ? this.selectedScript.playerCount : 6,
						price: this.selectedScript ? this.selectedScript.price : 88
					},
					{
						id: 3,
						startTime: '20:30',
						period: 'Â§úÂú∫',
						currentPlayers: 1,
						maxPlayers: this.selectedScript ? this.selectedScript.playerCount : 6,
						price: this.selectedScript ? this.selectedScript.price : 88
					}
				];
			},
			selectSession(session) {
				if (session.currentPlayers >= session.maxPlayers) {
					uni.showToast({
						title: 'ËØ•Âú∫Ê¨°Â∑≤Êª°',
						icon: 'none'
					});
					return;
				}
				this.selectedSession = session;
				this.playerCount = 1;
			},
			decreaseCount() {
				if (this.playerCount > 1) {
					this.playerCount--;
				}
			},
			increaseCount() {
				const maxCount = this.selectedSession.maxPlayers - this.selectedSession.currentPlayers;
				if (this.playerCount < maxCount) {
					this.playerCount++;
				} else {
					uni.showToast({
						title: `ÊúÄÂ§öËøòËÉΩÈ¢ÑÁ∫¶${maxCount}‰∫∫`,
						icon: 'none'
					});
				}
			},
			confirmReservation() {
				// È™åËØÅË°®Âçï
				if (!this.contactInfo.name.trim()) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•ÂßìÂêç',
						icon: 'none'
					});
					return;
				}
				
				if (!this.contactInfo.phone.trim() || !/^1[3-9]\d{9}$/.test(this.contactInfo.phone)) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÊâãÊú∫Âè∑',
						icon: 'none'
					});
					return;
				}
				
				// ÂàõÂª∫È¢ÑÁ∫¶
				const reservation = {
					scriptId: this.selectedScript.id,
					scriptName: this.selectedScript.name,
					sessionId: this.selectedSession.id,
					date: this.selectedDate,
					startTime: this.selectedSession.startTime,
					playerCount: this.playerCount,
					totalPrice: this.totalPrice,
					contactInfo: this.contactInfo
				};
				
				// ËøôÈáåÂèØ‰ª•Ë∞ÉÁî®ÂêéÁ´ØÊé•Âè£ÂàõÂª∫È¢ÑÁ∫¶
				console.log('ÂàõÂª∫È¢ÑÁ∫¶', reservation);
				
				uni.showModal({
					title: 'È¢ÑÁ∫¶ÊàêÂäü',
					content: `ÊÇ®Â∑≤ÊàêÂäüÈ¢ÑÁ∫¶${this.selectedScript.name}ÔºåÊó∂Èó¥Ôºö${this.selectedDate} ${this.selectedSession.startTime}`,
					showCancel: false,
					success: () => {
						// Ë∑≥ËΩ¨Âà∞ËÆ¢ÂçïÈ°µÈù¢
						uni.redirectTo({
							url: '/pages/orders/orders'
						});
					}
				});
			}
		}
	}
</script>

